import React, {useState} from "react";

import { LIST, KEYWORD } from "../data/storage";
import '../css/Products.css';

export default function Interior(){
    // 
    const [word, setWord] = useState('');
    const [search, setSearch] = useState('');
    const handlePick = (value) => setWord(value);
    const handleSearch = () => {
        setSearch(word);
    };
    const handleSearchEnter = (e) => {
        if (e.key === "Enter"){handleSearch()};
    };
    const interiorList = LIST.filter(
            (t) => (t.category === ''));
    const filterList = interiorList.filter(
        (t) => (t.name.includes(search) || t.size.includes(search) || t.category.includes(search)));
    //  
    const Reset = () => {
        setWord('');
        setSearch('');
    }
    
    return(
        <main className="products">
            <section className="products_wrap">
                <article id="products_title">
                    <h1></h1>
                    <hr/>
                    <h2>   !</h2>
                    <div id="products_search">
                        <input
                            type="text" placeholder="    "
                            value={word} onChange={(e) => setWord(e.target.value)} onKeyUp={(e) => handleSearchEnter(e)}/>
                        <div id="products_search_icon">
                            {word && (<img
                                src={`${process.env.PUBLIC_URL}/images/icon_reset_gray.png`} alt="icon_reset_gray"
                                onClick={Reset}/>)}
                            <img
                                src={`${process.env.PUBLIC_URL}/images/icon_magni_black.png`} alt="icon_magni_black"
                                onClick={handleSearch}/>
                        </div>
                    </div>
                    <div id="product_search_keyword">
                        {KEYWORD.map((t) => (
                            <button key={t} tabIndex={0} onClick={() => handlePick(t)}>
                                {t}
                            </button>
                        ))}
                    </div>
                </article>
                <article className="products_item">
                    {filterList.map((t)=>(
                        <div className="gallery_wrap" key={t.id}>
                            <div className="gallery">
                                <div className="g_img"><img src={t.image} alt={t.name}/></div>
                                <p className="txt1">{t.name}</p>
                                <p className="txt2">{t.price.toLocaleString()}</p>
                            </div>
                        </div>
                    ))}
                    {filterList.length === 0 && <p className="empty">  . </p>}
                </article>
            </section>
        </main>
    );
}
