import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";

import '../css/Header.css'

export default function Header({isLoggedIn,setIsLoggedIn,user}){
    const nav = useNavigate();

    //  
    const vwHeight = document.documentElement.clientHeight;
    const onScroll = () => {
        nav('/');
        window.scrollTo({ top: vwHeight, behavior: 'smooth' });
    };

    //  
    const [word, setWord] = useState('');
    const handleSearch = () => {
        nav('/product',{state:{word}});
        setWord('');
    };
    const handleSearchEnter = (e) => {
        if (e.key === "Enter"){handleSearch()};
    };

    return(
        <header>
            <div className="h_wrap_1">
                <div id="h_top">
                    <div id="h_logo">
                        <div className="icon_ho" onClick={onScroll}>
                            <img className="icon_white" src={`${process.env.PUBLIC_URL}/images/main_logo_black.png`} alt="main_logo_black"/>
                            <img className="icon_green" src={`${process.env.PUBLIC_URL}/images/main_logo_green.png`} alt="main_logo_green"/>
                        </div>
                    </div>
                    <ul id="h_menu_1">
                        <li><Link to='/product'></Link></li>
                        <li><Link to='/product/plant'></Link></li>
                        <li><Link to='/product/interior'></Link></li>
                        <li><Link to='/product/tool'></Link></li>
                        <li><Link to='/'></Link></li>
                    </ul>
                    <div id="h_menu_btn">
                        <img src={`${process.env.PUBLIC_URL}/images/icon_ham_black.png`} alt="icon_ham_black"/>
                    </div>
                    <div id="h_search">
                        <input
                            type="text" placeholder="find your plant"
                            value={word} onChange={(e) => setWord(e.target.value)} onKeyUp={(e) => handleSearchEnter(e)}/>
                        <img
                            src={`${process.env.PUBLIC_URL}/images/icon_magni_black.png`} alt="icon_magni_black"
                            onClick={handleSearch}/>
                    </div>
                </div>
            </div>
            <div className="h_wrap_2">
                <ul id="h_menu_2">
                    {isLoggedIn && user.name === '' &&
                        <li className="adminLabel"><Link to='/'></Link></li>}
                    {!isLoggedIn &&
                        <li><Link to='/login'></Link></li>}
                    {!isLoggedIn &&
                        <li><Link to='/signup'></Link></li>}
                    {isLoggedIn &&
                        <li onClick={()=>{setIsLoggedIn(false); nav('/')}}><Link to='/'></Link></li>}
                    <li><Link to='/' className="icon_ho"><img className="icon_white" src={`${process.env.PUBLIC_URL}/images/icon_heart_white.png`} alt="icon_heart_white"/><img className="icon_green" src={`${process.env.PUBLIC_URL}/images/icon_heart_green.png`} alt="icon_heart_green"/></Link></li>
                    <li><Link to='/' className="icon_ho"><img className="icon_white" src={`${process.env.PUBLIC_URL}/images/icon_cart_white.png`} alt="icon_cart_white"/><img className="icon_green" src={`${process.env.PUBLIC_URL}/images/icon_cart_green.png`} alt="icon_cart_green"/></Link></li>
                    <li><Link to='/' className="icon_ho"><img className="icon_white" src={`${process.env.PUBLIC_URL}/images/icon_person_white.png`} alt="icon_person_white"/><img className="icon_green" src={`${process.env.PUBLIC_URL}/images/icon_person_green.png`} alt="icon_person_green"/></Link></li>
                </ul>
            </div>
        </header>
    );
}
