import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import Swal from 'sweetalert2'

import '../css/Loginpage.css'

export default function Loginpage({setIsLoggedIn, setUser}){
    const nav = useNavigate();

    //  
    const KEY_ID = "KEY_ID";
    const KEY_SAVE_ID = "KEY_SAVE_ID";
    const [saveID, setSaveID] = useState(false);
    const handleSaveID = () => {
        localStorage.setItem(KEY_SAVE_ID, !saveID);
        setSaveID(!saveID);
    };
    useEffect(() => {
        let idFlag = JSON.parse(localStorage.getItem(KEY_SAVE_ID));
        if (idFlag !== null) setSaveID(idFlag);
        if (idFlag === false) localStorage.setItem(KEY_ID, "");

        let data = localStorage.getItem(KEY_ID);
        if (data !== null) setId(data);
    }, []);

    //  
    const [id, setId] = useState('');
    const [pw, setPw] = useState('');
    const handleLogin = (e) => {
        e.preventDefault();
        if(id === 'admin' && pw === '1234'){
            setIsLoggedIn(true);
            setUser({name:''});
            nav('/');
            if (saveID) localStorage.setItem(KEY_ID, id);
        } else if(id === 'user' && pw === '1234'){
            setIsLoggedIn(true);
            setUser({name:''});
            nav('/');
            if (saveID) localStorage.setItem(KEY_ID, id);
        } else {
            Swal.fire({
                text: '    .',
                confirmButtonText: '',
                scrollbarPadding: false,
                customClass: { popup: 'custom-popup', confirmButton: 'custom-button'}
            });
            setPw('');
        }
    };

    return(
        <main className="login">
            <div className="contents_wrap">
                <section className="login_wrap">
                    <form id="login" onSubmit={handleLogin}>
                        <h1>LOGIN</h1>
                        <ul>
                            <li>ID</li>
                            <li><input type="text" value={id}
                            onChange={(e)=>{setId(e.target.value)}} /></li>
                        </ul>
                        <ul>
                            <li>PASSWORD</li>
                            <li><input type="password" value={pw}
                            onChange={(e)=>{setPw(e.target.value)}} /></li>
                        </ul>
                        <label><input type="checkbox" checked={saveID} onChange={handleSaveID} /><p> </p></label>
                        <button></button>
                    </form>
                </section>
                <section className="find_wrap">
                    <article id="find">
                        <p>/ </p><Link to='/'>/ </Link>
                    </article>
                </section>
                <section className="signup_wrap">
                    <article id="signup">
                        <p>  </p><Link to='/signup'></Link>
                    </article>
                </section>
            </div>
        </main>
    );
}
