import React, { useState } from "react";
import { Routes, Route } from "react-router-dom";

import Header from './components/Header';
import Footer from "./components/Footer";

import Mainpage from './pages/Mainpage';
import Loginpage from './pages/Loginpage';
import Signuppage from './pages/Signuppage';
import ProductsIndex from "./pages_category/ProductsIndex";
import Products from './pages_category/Products';
import Plants from './pages_category/Plants';
import Interior from './pages_category/Interior';
import Tools from './pages_category/Tools';

import './App.css';

function App() {
  // 
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [user, setUser] = useState([]);

  return (
    <div className="App">
      <Header isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn} user={user}/>
      <main>
        <Routes>
          <Route path='/' element={<Mainpage />}/>
          <Route path='/login' element={<Loginpage setIsLoggedIn={setIsLoggedIn} setUser={setUser} />}/>
          <Route path='/signup' element={<Signuppage />}/>
          <Route path='/product' element={<ProductsIndex />}>
            <Route index element={<Products />}/>
            <Route path='plant' element={<Plants />}/>
            <Route path='interior' element={<Interior />}/>
            <Route path='tool' element={<Tools />}/>
          </Route>
          <Route path='/*' element={'Not Found'}/>
        </Routes>
      </main>
      <Footer/>
    </div>
  );
}

export default App;
